@font-face {
  font-family: Montserrat-Bold;
  src: url(../fonts/Montserrat-Bold.ttf); }

@font-face {
  font-family: Montserrat-Regular;
  src: url(../fonts/Montserrat-Regular.ttf); }

@font-face {
  font-family: Montserrat-Light;
  src: url(../fonts/Montserrat-Light.ttf); }

@font-face {
  font-family: PlayfairDisplay-Regular;
  src: url(../fonts/PlayfairDisplay-Regular.ttf); }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html, body {
  overflow-x: hidden; }

h1, h3 {
  font-family: "Montserrat-Regular";
  color: #403F3F; }

p {
  font-family: "PlayfairDisplay-Regular";
  color: #403F3F; }

a {
  text-decoration: none; }

button {
  font-family: "Montserrat-Bold"; }

button:focus {
  outline: 0; }

.text-center {
  text-align: center; }

.pointer {
  cursor: pointer; }

.slight-margin-left {
  margin-left: 20px; }

#trending-panel {
  width: 100%;
  height: 100%; }
  #trending-panel .card {
    min-height: 100px;
    width: 80%;
    background-color: #FFFEFE;
    border: none;
    border-radius: 10px;
    -webkit-box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.1);
            box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.1);
    margin: 10%;
    padding: 20px; }
    #trending-panel .card .title {
      margin-top: 0px;
      font-size: 15px; }
    #trending-panel .card .tags {
      margin-top: 20px;
      overflow: hidden; }
      #trending-panel .card .tags .tag {
        max-width: 95%;
        display: inline-block;
        margin: 2px;
        padding: 10px;
        font-size: 10px;
        border: 1px solid #7F0261;
        border-radius: 12px;
        color: #7F0261;
        -o-text-overflow: ellipsis;
           text-overflow: ellipsis;
        overflow: hidden;
        cursor: pointer; }



