@font-face {
  font-family: Montserrat-Bold;
  src: url(../fonts/Montserrat-Bold.ttf); }

@font-face {
  font-family: Montserrat-Regular;
  src: url(../fonts/Montserrat-Regular.ttf); }

@font-face {
  font-family: Montserrat-Light;
  src: url(../fonts/Montserrat-Light.ttf); }

@font-face {
  font-family: PlayfairDisplay-Regular;
  src: url(../fonts/PlayfairDisplay-Regular.ttf); }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html, body {
  overflow-x: hidden; }

h1, h3 {
  font-family: "Montserrat-Regular";
  color: #403F3F; }

p {
  font-family: "PlayfairDisplay-Regular";
  color: #403F3F; }

a {
  text-decoration: none; }

button {
  font-family: "Montserrat-Bold"; }

button:focus {
  outline: 0; }

.text-center {
  text-align: center; }

.pointer {
  cursor: pointer; }

.slight-margin-left {
  margin-left: 20px; }

#profile {
  height: 100%;
  width: 100%; }
  #profile .content {
    height: 100%;
    width: 100%; }
    #profile .content .left {
      display: inline-block;
      height: 100%;
      width: 20%;
      vertical-align: top; }
    #profile .content .right {
      display: inline-block;
      height: 100%;
      min-height: 100vh;
      width: 80%;
      padding-left: 5%;
      vertical-align: top; }
    #profile .content .arrows {
      width: 70%;
      font-size: 4em;
      text-align: center;
      color: #7F0261;
      margin-bottom: 25px; }
      #profile .content .arrows span {
        background: -webkit-linear-gradient(#7F0261, #F01263);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        cursor: pointer; }
      #profile .content .arrows .pad {
        padding: 10px 15px; }
      #profile .content .arrows .page {
        font-size: .3em; }

@media (max-width: 768px) {
  #profile .content .left {
    width: 100%;
    display: block; }
  #profile .content .right {
    width: 100%;
    padding: 0px;
    display: block;
    border: none; }
  #profile .content .arrows {
    width: 100%; } }



