@font-face {
  font-family: Montserrat-Bold;
  src: url(../fonts/Montserrat-Bold.ttf); }

@font-face {
  font-family: Montserrat-Regular;
  src: url(../fonts/Montserrat-Regular.ttf); }

@font-face {
  font-family: Montserrat-Light;
  src: url(../fonts/Montserrat-Light.ttf); }

@font-face {
  font-family: PlayfairDisplay-Regular;
  src: url(../fonts/PlayfairDisplay-Regular.ttf); }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html, body {
  overflow-x: hidden; }

h1, h3 {
  font-family: "Montserrat-Regular";
  color: #403F3F; }

p {
  font-family: "PlayfairDisplay-Regular";
  color: #403F3F; }

a {
  text-decoration: none; }

button {
  font-family: "Montserrat-Bold"; }

button:focus {
  outline: 0; }

.text-center {
  text-align: center; }

.pointer {
  cursor: pointer; }

.slight-margin-left {
  margin-left: 20px; }

#feed {
  height: 100%;
  width: 100%; }
  #feed .content {
    height: 100%;
    width: 100%; }
    #feed .content .left {
      display: inline-block;
      height: 100%;
      width: 20%;
      vertical-align: top; }
    #feed .content .right {
      display: inline-block;
      height: 100%;
      min-height: 100vh;
      width: 80%;
      padding-left: 5%;
      vertical-align: top; }

@media (max-width: 768px) {
  #feed .content .left {
    width: 0%;
    display: none; }
  #feed .content .right {
    width: 100%;
    padding: 0px; } }



