    #sidebar .search input:focus::-webkit-input-placeholder {
      color: #CECECE; }
    #sidebar .search .input:focus + .underline {
      -webkit-transform: scale(1);
          -ms-transform: scale(1);
              transform: scale(1); }
    #sidebar .search .underline {
      display: inline-block;
      position: absolute;
      height: 2px;
      width: 100%;
      top: 40px;
      left: 0px;
      background-color: #7F0261;
