@font-face {
  font-family: Montserrat-Bold;
  src: url(../fonts/Montserrat-Bold.ttf); }

@font-face {
  font-family: Montserrat-Regular;
  src: url(../fonts/Montserrat-Regular.ttf); }

@font-face {
  font-family: Montserrat-Light;
  src: url(../fonts/Montserrat-Light.ttf); }

@font-face {
  font-family: PlayfairDisplay-Regular;
  src: url(../fonts/PlayfairDisplay-Regular.ttf); }

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html, body {
  overflow-x: hidden; }

h1, h3 {
  font-family: "Montserrat-Regular";
  color: #403F3F; }

p {
  font-family: "PlayfairDisplay-Regular";
  color: #403F3F; }

a {
  text-decoration: none; }

button {
  font-family: "Montserrat-Bold"; }

button:focus {
  outline: 0; }

.text-center {
  text-align: center; }

.pointer {
  cursor: pointer; }

.slight-margin-left {
  margin-left: 20px; }

#user-card {
  width: 100%;
  height: 100%; }
  #user-card .card {
    min-height: 200px;
    width: 80%;
    background-color: #FFFEFE;
    border: none;
    border-radius: 10px;
    -webkit-box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.1);
            box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.1);
    margin: 10%;
    padding: 20px; }
    #user-card .card p {
      font-family: "Montserrat-Light"; }
    #user-card .card .frame {
      position: relative;
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 40%;
      border-radius: 50%;
      border: 1px solid #CECECE;
      overflow: hidden; }
      #user-card .card .frame:before {
        content: "";
        display: block;
        padding-top: 100%; }
      #user-card .card .frame .pic {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 100%;
        -o-object-fit: cover;
           object-fit: cover;
        -o-object-position: center center;
           object-position: center center;
        border-radius: 50%; }
    #user-card .card .name {
      color: #403F3F;
      text-align: center; }
    #user-card .card .joined {
      color: #403F3F;
      text-align: center;
      font-size: 0.7em; }
    #user-card .card .follow {
      display: block;
      margin: 20px;
      margin-left: auto;
      margin-right: auto;
      width: 80%;
      max-width: 150px;
      height: 30px;
      border-radius: 38.5px;
      font-size: 10px;
      border-width: 0;
      padding-top: .75em;
      padding-bottom: .75em;
      background-image: -webkit-gradient(linear, left top, right top, from(#7F0261), to(#F01263));
      background-image: -webkit-linear-gradient(left, #7F0261, #F01263);
      background-image: -o-linear-gradient(left, #7F0261, #F01263);
      background-image: linear-gradient(to right, #7F0261, #F01263);
      color: white; }
    #user-card .card .stats {
      display: block;
      margin-left: auto;
      margin-right: auto; }
      #user-card .card .stats .stat {
        display: inline-block;
        font-size: 0.5em;
        width: 33%;
        min-width: -webkit-max-content;
        min-width: -moz-max-content;
        min-width: max-content;
        text-align: center;
        overflow: hidden; }



