  float: left;
  width: 50%; }
  #landing .panels .panel .logo {
    margin-left: 80px;
    margin-top: 40px; }
  #landing .panels .panel .title {
    margin-top: 100px;
    margin-left: 80px;
    color: #403F3F; }
  #landing .panels .panel .description {
    margin-left: 80px;
    color: #403F3F; }
  #landing .panels .panel .login {
